* {
    padding: 0rem;
    margin: 0rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: calc(14px + 0.1vw);
   
  }
  .page{
    height: calc(10vh - 4rem);
  }
  
  h1 {
    font-size: 3em;
  }
  h2 {
    font-size: 2.6em;
  }
  h3 {
    font-size: 2.2em;
  }
  h4 {
    font-size: 1.8em;
  }
  h5 {
    font-size: 1.6em;
  }
  h6 {
    font-size: 1.4em;
  }
  text {
    font-size: 1em;
  }
  p {
    font-size: 1.2em;
  }